<template>
  <div>
    <div>
      <el-menu
        :default-active="this.$route.path"
        class="el-menu-demo"
        mode="horizontal"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
      >
        <el-menu-item index="1"></el-menu-item>
      </el-menu>
      <br />
      <!-- add -->
      <el-button type="primary" @click="add"></el-button>
      <el-table :data="tableData" style="text-align: center; width: 100%">
          <el-table-column
          prop="name"
          label=""
          width="50"
        ></el-table-column>
        <el-table-column
          prop="id"
          label=""
          width="50"
        ></el-table-column>
        <el-table-column
          prop="birthday"
          label=""
          width="50"
        ></el-table-column>
        <el-table-column prop="time" label="" width="40"></el-table-column>
        <el-table-column prop="lang" label="" width="50"></el-table-column>

        <el-table-column
          prop="country"
          label=""
          width="50"
        ></el-table-column>
        <el-table-column
          prop="brief"
          label=""
          width="300"
        ></el-table-column>
        <el-table-column prop="score" label="" width="40"></el-table-column>
        <el-table-column prop="pic" label="" width="50"></el-table-column>
        <el-table-column prop="cate" label="" width="50"></el-table-column>
        <el-table-column
          prop="campany"
          label=""
          width="100"
        ></el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
            <!-- 
            <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button> -->
            <el-button
              size="small"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- ,:visible.sync -->
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <!-- el-dialogel-form -->
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item
          label="()"
          prop="id"
          :label-width="formLabelWidth"
        >
          <el-input
            v-if="mark > 0"
            v-model="form.id"
            auto-complete="off"
          ></el-input>
          <el-input
            v-else
            v-model="form.id"
            auto-complete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="name"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="birthday"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.birthday" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="time"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.time" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="cate" :label-width="formLabelWidth">
          <el-select v-model="form.cate" placeholder="">
            <el-option
              v-for="(item,index) in cate"
              :key="index"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>
<!--
        <el-form-item
          label=""
          prop="pic"
          :label-width="formLabelWidth"
        >
          <el-upload
            class="avatar-uploader"
            action="http://121.5.109.66:8181/upload"
            :show-file-list="false"
            :on-remove="handleAvatarFail"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img
              v-if="form.pic"
              :src="require(`../assets/${form.pic}`)"
              class="avatar"
            />
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
-->


        <el-form-item
          label=""
          prop="campany"
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="form.campany"
            placeholder=""
      
          >
            <el-option
              v-for="item in campany"
              :key="item.index"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="lang" :label-width="formLabelWidth">
          <el-input v-model="form.lang" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="country" :label-width="formLabelWidth">
          <el-input v-model="form.country" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="brief" :label-width="formLabelWidth">
          <el-input v-model="form.brief" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="score" :label-width="formLabelWidth">
          <el-input v-model="form.score" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item style="text-align: right">
          <el-button @click="cancel"> </el-button>
          <!--  -->
          <el-button type="primary" @click="update('form')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: "managermovie",
  data() {
    return {
      cate: [
      //  {cate: ''}
      ],
      campany: [
      //   {campany: ''}
      ],
      // option2: [],

      loading: true,
      //   
      tableData: [
        {
          name: "",
          id: "",
          birthday: "",
          time: "",
          lang: "",
          country: "",
          brief: "",
          score: "",
          pic: "",
          cate: [],
          campany: [],
        },
      ],
      dialogFormVisible: false,
      formLabelWidth: "180px",
      // form
      form: {
        name: "",
        id: "",
        birthday: "",
        time: "",
        lang: "",
        country: "",
        brief: "",
        score: "",
        pic: "14.jpg",
        cate: "",
        campany: "",
      },
      rules: {
        id: [{ required: true, message: "", trigger: "blur" }],
        name: [{ required: true, message: "", trigger: "blur" }],
        birthday: [{ required: true, message: "", trigger: "blur" }],
        time: [{ required: true, message: "", trigger: "blur" }],
        lang: [{ required: true, message: "", trigger: "blur" }],
        // image:[
        //   { required: true, message: "", trigger: "blur" },
        // ],
        country: [{ required: true, message: "", trigger: "blur" }],
        brief: [{ required: true, message: "", trigger: "blur" }],
        score: [{ required: true, message: "", trigger: "blur" }],
        cate: [{ required: true, message: "", trigger: "blur" }],
        campany: [{ required: true, message: "", trigger: "blur" }],
      },
      currentIndex: "",
      mark: 0,
    };
  },
  created() {
    //   1.5loading
    setTimeout(() => {
      this.loading = false;
    }, 1500);
    this.getcate();
    this.getcampany();
    this.getTableData();
  },
  watch: {
    //2.xbug 1.x 5
    //listpage3
    total() {
      if (this.total == (this.page - 1) * this.size && this.total != 0) {
        this.page -= 1;
      }
    },
  },
  methods: {
    getcate() {
      const _this = this;
      axios.get("http://121.5.109.66:8181/getcateName").then(function (resp) {
        console.log(resp.data);
        _this.$data.cate = resp.data;
        console.log(_this.$data.cate);
      });

    },
    getcampany() {
      const _this = this;
      axios.get("http://121.5.109.66:8181/getcampanyName").then(function (resp) {
        console.log(resp.data);
        _this.$data.campany = resp.data;
        console.log(_this.$data.campany);
       
      }); 
    },
    getTableData() {
      const _this = this;
      axios.get("http://121.5.109.66:8181/getfilm").then(function (resp) {
        console.log(resp.data);
        _this.$data.tableData = resp.data;
        console.log(_this.$data.tableData);
        //var arr = []
        //arr.push({name:resp.data.name,id:resp.data.id,birthday:resp.data.birthday,lang:resp.data.lang,country:resp.data.country,brief:resp.data.brief,score:resp.data.score,pic:resp.data.pic})
        //this.$data.movies=arr;
        //console.log(this.$data.movies);
      });
    },

    // 
    add() {
      this.form = {
        name: "",
        id: "",
        birthday: "",
        time: "",
        lang: "",
        country: "",
        brief: "",
        score: "",
        pic: "14.jpg",
        cate: "",
        campany: "",
       
      };
      this.mark = 2;
      //   
      this.dialogFormVisible = true;
    },
    update(formName) {
      const _this=this;
      if (_this.mark > 1) _this.mark = 0;
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          axios
            .post("http://121.5.109.66:8181/updatefilm", _this.form)
            .then((resp) => {
              let data = resp.data.data;
              _this.tableData.push(_this.form);
              _this.dialogFormVisible = false;
                window.location.reload();
            });
        }
      });
    },
    handleEdit(index, row) {
      if (this.mark > 1) this.mark = 0;
      // index
      this.form = this.tableData[index + (this.page - 1) * this.size];
      // 
      this.dialogFormVisible = true;
    },
    handleDelete(index, row) {
      // console
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          const _this=this;
          axios
            .post("http://121.5.109.66:8181/deletefilm", row)
            .then((resp) => {
              _this.dialogFormVisible = false;
              window.location.reload();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    cancel() {
      if (this.mark > 1) this.mark = 0;
      // 
      this.dialogFormVisible = false;
    },
    handleSizeChange(val) {
      this.size = val;
    },
    handleCurrentChange(val) {
      this.page = val;
    },
    handleAvatarSuccess(res, file) {
      this.$message({
        type: "success",
        message: ",",
      });
      // this.form.image = URL.createObjectURL(file.raw);
      // console.log(this.form.image);
    },
    beforeAvatarUpload(file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      // if (!isJPG) {
      //   this.$message.error(' JPG !');
      // isJPG &&}
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isLt2M;
    },
    handleAvatarFail(file, fileList) {
      console.log("");
    },
  },
};
</script>
<style >
</style>

