
import Vue from 'vue'
import VueRouter from 'vue-router'


import Movies from '@/components/Movies'
import Actors from '@/components/Actors'
import Company from '@/components/Company'
//import Findfamily from '@/components/Findfamily'
//import Findname from '@/components/Findname'
import Findactors from '@/components/Findactors'
import Findcompany from '@/components/Findcompany'
import Context from '@/components/Context'
import moviedetial from '@/components/moviedetial'
import campanydetail from '@/components/campanydetail'
import actordetail from '@/components/actordetail'
//import Findname2 from '@/components/Findname2'
import store from '@/store/index.js'
import Login from '@/components/Login'
import managermovie from '@/components/managermovie'
import managecate from '@/components/managecate'
import managecampany from '@/components/managecampany'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: '',
    component: Context,
    redirect: '/Movies',
    children: [
      {
        path: '/Movies',
        name: '',
        component: Movies,
        children: [

        ]
      },
      {
        path: '/moviedetail',
        name: '',
        component: moviedetial
      }/*,
      {
        path: '/Findname2',
        name: '2',
        component: Findname2
      }*/
    ]
  },
  {
    path: '/',
    name: '',
    component: Context,
    redirect: '/Actors',
    children: [
      {
        path: '/Actors',
        name: '',
        component: Actors,
        children: []
      },
      {
        path: '/actordetail',
        name: '',
        component: actordetail
      },
      {
        path: '/Findactors',
        name: '',
        component: Findactors
      },
    ]
  },
  {
    path: '/',
    name: '',
    component: Context,
    redirect: '/Company',
    children: [
      {
        path: '/Company',
        name: '',
        component: Company,
        children: []
      },
      {
        path: '/campanydetail',
        name: '',
        component: campanydetail

      },
      {
        path: '/FindCompany',
        name: '',
        component: Findcompany
      },
    ]
  },
  {
    path: '/',
    name: '',
    component: Context,
    redirect: '/Login',
    children: [
      {
        path: '/Login',
        name: '',
        component: Login,
        children: [

        ]
      },
      {
        path: '/managermovie',
        name: '',
        component: managermovie
      },
      {
        path: '/managecate',
        name: '',
        component: managecate
      },
      {
        path: '/managecampany',
        name: '',
        component: managecampany
      },
    ]
  },
]

const router = new VueRouter({
 // mode: 'history',
 mode:'hash',
  base: process.env.BASE_URL,
  routes

})


const whiteList = ['/Movies', '/campanydetail', '/moviedetail', '/actordetail', '/Findactors', '/FindCompany', '/Login', '/Actors', '/Company'] // 
router.beforeEach((to, from, next) => {
  // to 
  // from 
  //  next() 
  let token = ''
  let manager = ''
  try {
    let userInfo = JSON.parse(sessionStorage.userInfo) //  sessionStorage 
    //  vuex  sessionStorage  vuex 
    store.dispatch('setUserInfo', userInfo)
    token = userInfo.token
    manager = userInfo.manager
    console.log(token);
    console.log(manager);    
  } catch (error) {
    token = ''
  }
  if (token) {
    if(manager){
      next();
      
    }
  } else {
    //  token
    if (whiteList.indexOf(to.path) !== -1) {
      // 
      next()
    } else if (to.path === '/managermovie'||to.path === '/managecate'||to.path==='/managecampany') {
      next('/Login');
    }
    else {
      // 
      next('/login?redirect=${to.path}')
    }
  }
})

export default router