<template>
  <div>
    <div>
      <el-menu
        :default-active="this.$route.path"
        class="el-menu-demo"
        mode="horizontal"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
      >
        <el-menu-item index="1"></el-menu-item>
      </el-menu>
      <br />
      <!-- add -->
      <el-button type="primary" @click="add"></el-button>
      <el-table :data="tableData" style="text-align: center; width: 100%">
        <!--        <el-table-column prop="id" label="" width="180"></el-table-columntype="index">-->
        <el-table-column
          prop="id"
          label=""
          width="150"
        ></el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="150"
        ></el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              size="small"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- ,:visible.sync -->
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <!-- el-dialogel-form -->
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item
          label="()"
          prop="id"
          :label-width="formLabelWidth"
        >
          <el-input
            v-if="mark > 1"
            v-model="form.id"
            auto-complete="off"
          ></el-input>
          <el-input
            v-else
            v-model="form.id"
            auto-complete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="name"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item style="text-align: right">
          <el-button @click="cancel"> </el-button>
          <!--  -->
          <el-button type="primary" @click="update('form')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name:'managecate',
  data() {
    return {
size:'',
page:'',
      loading: true,
      //   
      tableData: [
        {
          id:'',
          name:'',
        }
      ],
      dialogFormVisible: false,
      formLabelWidth: "180px",
      // form
      form: {
        id: "",
        name: "",
      },
      rules:{
        id: [
          { required: true, message: "", trigger: "blur" },
        ],
        name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      currentIndex: "",
      mark:0
    };
  },
  created() {
    //   1.5loading
    setTimeout(() => {
      this.loading = false;
    }, 1500);
    this.getTableData();
  },
  watch:{
    //2.xbug 1.x 5
    //listpage3
  },
  methods: {
    getTableData(){
       const _this = this;
      axios.get("http://121.5.109.66:8181/getcate").then(function (resp) {
        console.log(resp.data);
        _this.$data.tableData = resp.data;
        console.log(_this.$data.tableData);

      });
    },
    // 
    add() {
      this.form = {
        id: "",
        name: "",
      };
      this.mark=2;
      //   
      this.dialogFormVisible = true;
    },
    update(formName) {
      const _this=this;
      if (_this.mark > 1) _this.mark = 0;
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          axios
            .post("http://121.5.109.66:8181/updatecate", _this.form)
            .then((resp) => {
              let data = resp.data.data;
              _this.tableData.push(_this.form);
              _this.dialogFormVisible = false;
                window.location.reload();
            });
        }s
      });
      },
    handleEdit(index, row) {
      if(this.mark>1) this.mark=0;
      // index
      this.form = this.tableData[index+(this.page-1)*this.size];
      // 
      this.dialogFormVisible = true;
    },
    handleDelete(index, row) {
      // console
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          const _this=this;
          axios
            .post("http://121.5.109.66:8181/deletecate", row)
            .then((resp) => {
              _this.dialogFormVisible = false;
              window.location.reload();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    cancel() {
      if(this.mark>1) this.mark=0;
      // 
      this.dialogFormVisible = false;
    },
    handleSizeChange(val){
      this.size = val;
    },
    handleCurrentChange(val) {
      this.page = val;
    }
  }
};
</script>
